* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.navigation {
  background-color: rgb(236, 139, 100);
  width: 90%;
  height: 50px;
  padding: 0 5%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation ul {
  list-style-type: none;
  display: flex;
  margin: 0 10px;
}

.navigation li {
  margin: 0 10px;
  color: aliceblue;
}

.text {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 90vh;
  justify-content: center;
  align-items: center;
}

.text h2 {
  font-size: 36px;
  margin: 20px;
}

.text p {
  font-size: 24px;
  width: 30vw;
  text-align: center;
}

.footer {
  width: 90%;
  height: 30px;
  padding: 0 5%;
  background-color: rgb(236, 139, 100);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
}

.starting {
  background-image: url("../src/images/laptop.jpg");
}

@media only screen and (max-width: 900px) {
  .text h2 {
    font-size: 26px;
  }

  .text p {
    font-size: 18px;
  }
}
